body {
  background: #222;
  width: 100%;
  height: 100%;
}
.warpper {
  margin-top: 50px;
  width: 100%;
  display: grid;
  grid-column-gap: 1em;
  grid-template-columns: 1fr 1fr;
}
.warpper div {
  background: #eee;
}
.navbar {
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  background: #9f9;
}
